'use client';
import React, { useEffect, useRef, useState } from 'react';
import apiClient from '../lib/api-client';

type WO = {
  id: string | number;
  doc_no?: string;
  asset_name?: string;
  work_type?: string;
  start_date?: string | null;
  end_date?: string | null;
  description?: string | null;
  raw?: any;
};

// Warna berdasarkan status / type
function pickColor(w: WO) {
  const wt = (w.work_type || (w.raw?.work_type || w.raw?.type_work) || '')
    .toString()
    .toLowerCase();
  const status = (w.raw?.doc_status || '').toLowerCase();

  if (status.includes('completed')) return '#10b981';
  if (status.includes('draft')) return '#9ca3af';
  if (wt.includes('preventive') || wt === 'pm') return '#16a34a';
  if (wt.includes('corrective') || wt === 'cm') return '#f97316';
  if (wt.includes('breakdown') || wt.includes('failure')) return '#ef4444';
  return '#3b82f6';
}

function toDate(iso?: string | null) {
  if (!iso) return null;
  const d = new Date(iso);
  return isNaN(d.getTime()) ? null : d;
}

export default function VisTimelineGantt({ pageSize = 2000 }: { pageSize?: number }) {
  const containerRef = useRef<HTMLDivElement | null>(null);
  const tlRef = useRef<any>(null);
  const customNowIdRef = useRef<string | null>(null);

  const [itemsRaw, setItemsRaw] = useState<WO[]>([]);
  const [selectedDate, setSelectedDate] = useState<string>(
    new Date().toISOString().slice(0, 10)
  );
  const [loading, setLoading] = useState(false);
  const [selected, setSelected] = useState<WO | null>(null);
  const [fullscreen, setFullscreen] = useState(false);

  // inject vis-timeline CSS sekali
  useEffect(() => {
    const cssHref =
      'https://unpkg.com/vis-timeline@latest/dist/vis-timeline-graph2d.min.css';
    if (!document.querySelector(`link[href="${cssHref}"]`)) {
      const l = document.createElement('link');
      l.rel = 'stylesheet';
      l.href = cssHref;
      document.head.appendChild(l);
    }
  }, []);

  // Load data
  useEffect(() => {
    load();
  }, []);

  async function load() {
    setLoading(true);
    try {
      const res = await apiClient(`/work-orders?q=&page=1&pageSize=${pageSize}`);
      const rows = res?.data ?? res;

      const mapped: WO[] = (rows || []).map((r: any) => ({
        ...r,
        start_date: r.start_date,
        end_date: r.end_date,
        description: r.description ?? r.raw?.description ?? ''
      }));

      setItemsRaw(mapped);
    } catch (e) {
      console.error('load error', e);
    } finally {
      setLoading(false);
    }
  }

  // Build data per-hari
  function buildDayData(dateStr: string) {
    const dayStart = new Date(`${dateStr}T00:00:00`).getTime();
    const dayEnd = dayStart + 24 * 60 * 60 * 1000;

    const filtered = itemsRaw
      .filter((it) => {
        const s = toDate(it.start_date)?.getTime() ?? null;
        const e = toDate(it.end_date)?.getTime() ?? null;
        if (!s && !e) return false;
        const start = s ?? e!;
        const end = e ?? s!;
        return !(end <= dayStart || start >= dayEnd);
      })
      .sort((a, b) => {
        const sa = toDate(a.start_date)?.getTime() ?? 0;
        const sb = toDate(b.start_date)?.getTime() ?? 0;
        return sa - sb;
      });

    const groups = filtered.map((it) => ({
      id: String(it.id),
      content: `${it.doc_no} â€” ${it.asset_name}`
    }));

    const items = filtered.map((it) => {
      const sRaw = toDate(it.start_date)?.getTime() ?? null;
      const eRaw = toDate(it.end_date)?.getTime() ?? null;
      const start = Math.max(sRaw ?? eRaw ?? dayStart, dayStart);
      const end = Math.min(eRaw ?? sRaw ?? dayEnd, dayEnd);
      return {
        id: String(it.id),
        group: String(it.id),
        start: new Date(start),
        end: new Date(end),
        content: it.doc_no,
        className: `wo-${String(it.id)}`,
        _raw: it
      };
    });

    return { groups, items, dayStart, dayEnd };
  }

  // Mount Vis Timeline
  useEffect(() => {
    async function mount() {
      if (!containerRef.current) return;

      // dynamic script load
      const visUrl =
        'https://unpkg.com/vis-timeline@latest/dist/vis-timeline-graph2d.min.js';
      if (!(window as any).vis) {
        await new Promise<void>((resolve) => {
          const s = document.createElement('script');
          s.src = visUrl;
          s.async = true;
          s.onload = () => resolve();
          document.body.appendChild(s);
        });
      }

      const vis = (window as any).vis;
      if (!vis?.Timeline) return;

      containerRef.current.innerHTML = '';

      const { groups, items, dayStart, dayEnd } = buildDayData(selectedDate);

      const DataSet = vis.DataSet;
      const groupsDs = new DataSet(groups);
      const itemsDs = new DataSet(items);

      const options = {
        stack: false,
        start: new Date(dayStart),
        end: new Date(dayEnd),
        min: new Date(dayStart),
        max: new Date(dayEnd),
        zoomMin: 30 * 60 * 1000,
        zoomMax: 24 * 60 * 60 * 1000,
        orientation: 'top',
        editable: false
      };

      const timeline = new vis.Timeline(
        containerRef.current,
        itemsDs,
        groupsDs,
        options
      );
      tlRef.current = timeline;

      // Tambah garis NOW
      const now = Date.now();
      if (now >= dayStart && now <= dayEnd) {
        const id = 'now-marker';
        timeline.addCustomTime(new Date(), id);

        setTimeout(() => {
          const el = containerRef.current!.querySelector('.vis-custom-time');
          if (el) {
            (el as HTMLElement).style.borderLeft = '2px dashed #ef4444';
            (el as HTMLElement).style.height = '100%';
            (el as HTMLElement).style.zIndex = '999';
          }
        }, 80);

        customNowIdRef.current = id;
      }

      // Pewarnaan item
      items.forEach((it: any) => {
        const color = pickColor(it._raw);
        addCssRule(
          `.vis-item.wo-${it.id} .vis-item-content`,
          `background:${color}!important;color:#fff;border-radius:4px;`
        );
      });

      // Click listener
      timeline.on('select', (props: any) => {
        if (!props.items.length) return;
        const id = props.items[0];
        const row = items.find((it: any) => it.id === id);
        if (row) setSelected(row._raw);
      });
    }

    mount();
  }, [itemsRaw, selectedDate]);

  // Helper inject CSS
  function addCssRule(selector: string, rule: string) {
    const styleId = 'vis-dynamic-style';
    let node = document.getElementById(styleId) as HTMLStyleElement;
    if (!node) {
      node = document.createElement('style');
      node.id = styleId;
      document.head.appendChild(node);
    }
    node.appendChild(document.createTextNode(`${selector}{${rule}}`));
  }

  // FULLSCREEN toggle
  function toggleFullscreen() {
    setFullscreen((f) => !f);
  }

  return (
    <div className={fullscreen ? 'gantt-fullscreen' : ''}>
      <div className="gantt-wrapper">
        {/* Toolbar */}
        <div className="gantt-toolbar">
          <h2>Work Order Gantt</h2>

          <div className="toolbar-right">
            <input
              type="date"
              value={selectedDate}
              onChange={(e) => setSelectedDate(e.target.value)}
              className="date-input"
            />

            <button onClick={() => load()} className="btn small">
              Refresh
            </button>

            <button onClick={toggleFullscreen} className="btn small blue">
              {fullscreen ? 'Exit Fullscreen' : 'Fullscreen'}
            </button>
          </div>
        </div>

        {/* Container */}
        <div ref={containerRef} className="gantt-container" />

        {/* Modal Detail */}
        {selected && (
          <div className="modal-overlay">
            <div className="modal-card">
              <h3>{selected.doc_no}</h3>
              <p>
                <strong>Asset:</strong> {selected.asset_name}
              </p>
              <p>
                <strong>Type:</strong> {selected.work_type}
              </p>
              <p>
                <strong>Start:</strong> {selected.start_date}
              </p>
              <p>
                <strong>End:</strong> {selected.end_date}
              </p>

              <pre className="modal-json">
                {JSON.stringify(selected.raw ?? selected, null, 2)}
              </pre>

              <button onClick={() => setSelected(null)} className="btn">
                Close
              </button>
            </div>
          </div>
        )}
      </div>

      {/* Extra styling */}
      <style jsx>{`
        .gantt-wrapper {
          background: #ffffff;
          padding: 16px;
          border-radius: 12px;
          box-shadow: 0 4px 14px rgba(0, 0, 0, 0.1);
        }

        .gantt-toolbar {
          display: flex;
          justify-content: space-between;
          align-items: center;
          margin-bottom: 12px;
        }

        .toolbar-right {
          display: flex;
          gap: 10px;
          align-items: center;
        }

        .date-input {
          padding: 6px 10px;
          border-radius: 6px;
          border: 1px solid #ddd;
        }

        .btn {
          padding: 6px 12px;
          border-radius: 6px;
          border: none;
          cursor: pointer;
          background: #444;
          color: #fff;
        }
        .btn.small {
          font-size: 0.8rem;
        }
        .btn.blue {
          background: #2563eb;
        }

        .gantt-container {
          height: 500px;
        }

        /* FULLSCREEN MODE */
        .gantt-fullscreen {
          position: fixed;
          left: 0;
          top: 0;
          width: 100vw;
          height: 100vh;
          background: #fff;
          z-index: 99999;
          padding: 20px;
          overflow: auto;
        }

        /* MODAL STYLES */
        .modal-overlay {
          position: fixed;
          left: 0;
          top: 0;
          width: 100vw;
          height: 100vh;
          background: rgba(0, 0, 0, 0.45);
          display: flex;
          justify-content: center;
          align-items: center;
          z-index: 99999;
        }

        .modal-card {
          background: #fff;
          padding: 20px;
          width: 700px;
          max-height: 80vh;
          overflow-y: auto;
          border-radius: 10px;
          box-shadow: 0 4px 14px rgba(0, 0, 0, 0.25);
        }

        .modal-json {
          background: #f7f7f7;
          padding: 10px;
          border-radius: 8px;
          white-space: pre-wrap;
          margin-top: 10px;
        }
      `}</style>
    </div>
  );
}
